<!-- 搜索页 -->
<template>
  <div class="search">
    <div class="apply-dialog" v-show="isDialogShow">
      <div class="dialog">
        <div class="input">
          <textarea v-model="applyVal" maxlength="30" placeholder="如申请开通视图权限，请输入开通理由"></textarea>
          <span>30字以内</span>
        </div>
        <div class="btn" @click="submitApply">提交</div>
        <div class="close" @click="isDialogShow = false"></div>
      </div>
    </div>
    <SearchBar @searchVal="searchVal" @keyboardSearch="keyboardSearch" >
      <div class="search-btn" @click="toSearchResultPage">搜索</div>
    </SearchBar>
    <div class="content">
      <div class="search-list" v-show="inputVal.length > 0">
        <div class="loading" v-show="isLoadingShow">
          <img src="../../assets/images/loading.gif" alt />
          <p>搜索中...</p>
        </div>
        <div class="empty" v-show="isEmptyShow && searchViewResultList.length === 0">搜索结果为空</div>
        <div class="search-result" v-show="!isLoadingShow">
          <div class="view" v-show="searchViewResultList.length">
            <h5><i />相关视图</h5>
            <ul>
              <li v-for="(item, index) in searchViewResultList" :key="index" @click="toLink(searchViewResultList[index], 'view')">
                <span class="left">
                  <span class="text" v-html="item.text"></span>
                  <img v-show="item.type !== 'keyWord'" :src="item.image" alt="">
                </span>
                <span class="auth">
                  <span v-show="item.type === 'keyWord'" class="key-icon">关键字</span>
                  <!-- <span v-show="item.type === 'keyWord'">&#8594;&nbsp;{{item.modulename}}</span> -->
                  <span v-show="item.quanxianC === 0">&nbsp;{无权限}</span>
                </span>
              </li>
            </ul>
            <ul v-show="searchMoreViewResultList.length && isShowMore">
              <li v-for="(item, index) in searchMoreViewResultList" :key="index" @click="toLink(searchMoreViewResultList[index], 'view')">
                <span class="left">
                  <span class="text" v-html="item.text"></span>
                  <img v-show="item.type !== 'keyWord'" :src="item.image" alt="">
                </span>
                <span class="auth">
                  <span v-show="item.type === 'keyWord'" class="key-icon">关键字</span>
                  <span v-show="item.quanxianC === 0">&nbsp;{无权限}</span>
                </span>
              </li>
            </ul>
          </div>
          <div class="more" @click="showMore" v-if="searchMoreViewResultList.length">
            {{isShowMore ? '收起' : '更多'}}
            <img :class="[isShowMore ? 'roate' : '']" src="../../assets/images/icon_show@2x.png" alt="">
          </div>
          <div class="article" v-show="searchArticleResultList.length">
            <h5><i />相关文章</h5>
            <ul>
              <li v-for="(item, index) in searchArticleResultList" :key="index" @click="toLink(searchArticleResultList[index], 'article')">
                <span class="left">
                  <span class="text" v-html="item.text"></span>
                </span>
                <span class="auth">
                  <span v-show="item.type === 'keyWord'" class="key-icon">关键字</span>
                </span>
              </li>
            </ul>
          </div>
        </div>
        <!-- <ul v-show="!isLoadingShow">
          <li v-for="(item, index) in searchResultList" :key="index" v-html="item"></li>
        </ul> -->
      </div>
      <div class="history-hot" v-show="inputVal.length === 0">
        <div class="history" v-show="isHistoryShow && searchHistoryList.length">
          <span class="delete-btn" @click="hideHistory"></span>
          <h4>历史搜索</h4>
          <ul>
            <li @click="clickToResult(item.searchtext)" v-for="(item, index) in searchHistoryList" :key="index">{{item.searchtext}}</li>
          </ul>
        </div>
        <div class="hot">
          <h4>热门搜索</h4>
          <ul>
            <li @click="clickToResult(item.searchtext)" v-for="(item, index) in searchHotList" :key="index">{{item.searchtext}}</li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from 'vant';
import SearchBar from "../../components/SearchBar";
export default {
  name: "Search",
  components: {
    SearchBar
  },
  data() {
    return {
      inputVal: "",
      applyVal: '',
      isDialogShow: false,
      isLoadingShow: false,
      isEmptyShow: false,
      isHistoryShow: true,
      // allList: ["交易总览", "设计初衷", "块级作用域", "交通银行"],
      isShowMore: false,
      searchViewResultList: [],
      searchMoreViewResultList: [],
      searchArticleResultList: [],
      searchHistoryList: [],
      searchHotList: []
    };
  },
  created() {},
  mounted() {
    this.getHistory();
    this.getHot();
  },
  methods: {
    // 获取搜索框值
    searchVal(val) {
      this.inputVal = val;
      this.searchViewResultList = [];
      if (this.inputVal) {
        this.isLoadingShow = true;
        this.isEmptyShow = false;
        const reg = new RegExp(this.inputVal, "g");
        this.$axios.post('/mobile/hopeModule/searchModuleByName', {
          "name": this.inputVal,
          "aamid": this.$store.state.userInfoObj.aamid,
          "deptid": this.$store.state.userInfoObj.deptid,
					"odeptid": this.$store.state.userInfoObj.odeptid
          // "aamid": "888803996",
					// "deptid": "00111002",
					// "odeptid": "0010101032"
        }).then(res => {
        	this.isLoadingShow = false;
        	if (res.status === 200 && res.data.status === '000000') {
            const resData = res.data.data || [];
        		const viewKeyList = resData.module.list[1].styleList || [];
            const viewTableList = resData.module.list[0].styleList || [];
            const articleKeyList = resData.article.list[1].styleList || [];
            const articleTableList = resData.article.list[0].styleList || [];
            viewKeyList.forEach(item => {
              item.type = 'keyWord';
            });
            articleKeyList.forEach(item => {
              item.type = 'keyWord';
            });
            const showViewList = viewTableList.slice(0, 3).concat(viewKeyList.slice(0, 3));
            const articleList = articleTableList.concat(articleKeyList);
            const moreViewList = viewKeyList.slice(3).concat(viewTableList.slice(3));

            if (viewKeyList.length === 0 && viewTableList.length === 0 && articleKeyList.length === 0 && articleTableList.length === 0) {
              this.isEmptyShow = true;
              return;
            }
            // 关键字高亮
            this.searchViewResultList = showViewList.map(i => {
              return {
                text: i.type && i.type === 'keyWord' ? i.keyname.replace(
                  reg,
                  "<span style='color: #45A4B1;'>" + this.inputVal + "</span>"
                ) : i.modulename.replace(
                  reg,
                  "<span style='color: #45A4B1;'>" + this.inputVal + "</span>"
                ),
                ...i
              };
            });
            this.searchMoreViewResultList = moreViewList.map(i => {
              return {
                text: i.type && i.type === 'keyWord' ? i.keyname.replace(
                  reg,
                  "<span style='color: #45A4B1;'>" + this.inputVal + "</span>"
                ) : i.modulename.replace(
                  reg,
                  "<span style='color: #45A4B1;'>" + this.inputVal + "</span>"
                ),
                ...i
              };
            });
            this.searchArticleResultList = articleList.map(i => {
              return {
                text: i.textname.replace(
                  reg,
                  "<span style='color: #45A4B1;'>" + this.inputVal + "</span>"
                ),
                ...i
              };
            });
        	} else {
        		this.isEmptyShow = true;
        	}
        }).catch(err => {
        	this.isLoadingShow = false;
        	this.isEmptyShow = true;
        });
      }
    },
    // 点击展示更多
    showMore() {
      this.isShowMore = !this.isShowMore;
    },
    // 跳转到搜索结果页
    toSearchResultPage() {
      this.inputVal && this.$router.push({name: 'SearchResult', query: {value: this.inputVal}});
    },
    // 键盘搜索事件
    keyboardSearch() {
      this.toSearchResultPage();
    },
    // 点击历史/热门去结果页
    clickToResult(text) {
      this.$router.push({name: 'SearchResult', query: {value: text}});
    },
    // 跳转到详情
    toLink(item, type) {
      const path = type === 'view' ? item.url : item.textpath;
      if (item.quanxianC === 0) {
        this.isDialogShow = true;
        return;
      }
      if (item.type === 'keyWord') {
        const value = type === 'article' ? item.textname : item.modulename;
        this.$router.push({name: 'SearchResult', query: { value, isArticle: type === 'article' }});
        return;
      }
      window.location.href = path;
    },
    // 提交申请
    submitApply() {
      const val = this.applyVal.trim();
      if (!val) {
        Toast.fail('请输入开通理由');
        return;
      }
      this.isDialogShow = false;
    },
    // 获取搜索历史
    getHistory() {
      this.$axios
        .post("/mobile/hopeSearchHistory/querySearchRecord", {
          "aamid": this.$store.state.userInfoObj.aamid,
          // aamid: "888803996"
        })
        .then(res => {
          if (res.status === 200 && res.data.status === "000000") {
            const resData = res.data.data || [];
            this.searchHistoryList = [...resData];
          }
        });
    },
    // 获取热门推荐
    getHot() {
      this.$axios
        .post("/mobile/hopeSearchHistory_h/queryHotSearch", {
          "aamid": this.$store.state.userInfoObj.aamid,
          // aamid: "888803996"
        })
        .then(res => {
          if (res.status === 200 && res.data.status === "000000") {
            const resData = res.data.data || [];
            this.searchHotList = [...resData];
          }
        });
    },
    hideHistory() {
      const searchArr = this.searchHistoryList.map(i => i.searchtext);
      this.$axios
        .post("/mobile/hopeSearchHistory/delSearchRecord", {
          "aamid": this.$store.state.userInfoObj.aamid,
          // aamid: "888803996",
          searchArr
        })
        .then(res => {
          if (res.status === 200 && res.data.status === "000000") {
            isHistoryShow = false;
          }
        });
    }
  }
};
</script>
<style lang="less" scoped>
.search {
  min-height: 100%;
  background: #fff;
}
.apply-dialog{
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 999;
  .dialog{
    width: 3.4rem;
    position: absolute;
    top: 20%;
    left: 50%;
    transform: translateX(-50%);
    .input{
      height: 1.9rem;
      padding: 0.12rem 0.15rem;
      position: relative;
      background: #fff;
      border: 1px solid #E0E0E0;
      border-radius: 4px;
      textarea{
        width: 100%;
        font-size: 0.15rem;
        color: #969696;
      }
      span{
        position: absolute;
        font-size: 12px;
        color: #A6A6A6;
        right: 0.1rem;
        bottom: 0.1rem;
      }
    }
    .btn{
      background-image: linear-gradient(270deg, #45A4B1 0%, #2A7875 100%);
      border-radius: 4px;
      height: 0.44rem;
      line-height: 0.44rem;
      text-align: center;
      font-size: 0.17rem;
      color: #FFFFFF;
      margin: 0.2rem 0 0.35rem;
    }
    .close{
      width: 0.7rem;
      height: 0.7rem;
      background: url('../../assets/images/btn_close@2x.png') no-repeat;
      background-size: 0.7rem 0.7rem;
      margin: 0 auto;
    }
  }
}
.search-btn{
  width: 0.52rem;
  height: 0.3rem;
  background: #45A4B1;
  border-radius: 0.15rem;
  color: #fff;
  text-align: center;
  line-height: 0.3rem;
  font-size: 0.15rem;
  margin-left: 0.06rem;
}
.content {
  .search-list {
    .loading {
      padding-top: 1.2rem;
      text-align: center;
      font-size: 0.14rem;
      color: #666666;
      img {
        width: 0.4rem;
      }
    }
    .empty {
      padding-top: 1.2rem;
      text-align: center;
      font-size: 0.14rem;
      color: #666666;
    }
    li {
      height: 0.44rem;
      line-height: 0.44rem;
      font-size: 0.15rem;
      color: #232323;
      border-bottom: 1px solid #eaeaea;
      box-sizing: border-box;
      padding: 0 0.1rem 0 0.17rem;
      display: flex;
      align-items: center;
      position: relative;
      overflow: hidden;
      .left{
        flex: 1;
        display: flex;
        align-items: center;
        overflow: hidden;
        height: 0.44rem;
        .text{
          height: 0.44rem;
          overflow: hidden;
          text-overflow:ellipsis;
          white-space: nowrap;
          // max-width: calc(100% - 0.6rem);
          padding-right: 0.09rem;
          box-sizing: border-box;
        }
      }
      img{
        width: 0.3rem;
        height: 0.3rem;
        margin-left: 0.1rem;
      }
      .auth{
        float: right;
        font-size: 0.12rem;
        color: #969696;
      }
      .key-icon{
        display: inline-block;
        width: 0.5rem;
        height: 0.16rem;
        border: 1px solid #45A4B1;
        border-radius: 0.08rem;
        font-size: 0.12rem;
        color: #45A4B1;
        line-height: 0.16rem;
        text-align: center;
        white-space: nowrap;
      }
    }
  }
  .search-result{
    h5{
      height: 0.4rem;
      font-size: 0.14rem;
      color: #969696;
      line-height: 0.4rem;
      position: relative;
      text-indent: 0.16rem;
      border-bottom: 1px solid #EAEAEA;
      i{
        position: absolute;
        width: 0.04rem;
        height: 0.24rem;
        background: #2A7875;
        left: 0;
        top: 0.08rem;
      }
    }
  }
  .more{
    height: 0.16rem;
    padding-top: 0.1rem;
    font-size: 0.11rem;
    color: #45A4B1;
    display: flex;
    justify-content: center;
    align-items: center;
    img{
      width: 0.12rem;
      height: 0.13rem;
      margin-left: 0.02rem;
    }
    .roate{
      transform: rotate(180deg);
    }
  }
  .history-hot {
    padding: 0.1rem 0.16rem 0 0.16rem;
    h4 {
      font-size: 0.14rem;
      height: 0.2rem;
      line-height: 0.2rem;
      color: #232323;
      margin-bottom: 0.12rem;
    }
    ul {
      overflow: hidden;
      li {
        font-size: 0.13rem;
        color: #666666;
        padding: 0.06rem 0.15rem;
        background: #f1f1f1;
        border-radius: 0.15rem;
        float: left;
        margin-right: 0.06rem;
        margin-bottom: 0.06rem;
      }
    }
    .history {
      position: relative;
      margin-bottom: 0.18rem;
      .delete-btn {
        position: absolute;
        width: 0.22rem;
        height: 0.22rem;
        display: block;
        top: -0.02rem;
        right: -0.04rem;
        background: url("../../assets/images/icon_delete@2x.png") no-repeat center
          center;
        background-size: 0.22rem 0.22rem;
      }
    }
  }
}
</style>